import React from "react"
import PropTypes from "prop-types"

// Utilities
import {kebabCase, toLower} from "lodash"
import Layout from "../components/layout"

// Components
import { Helmet } from "react-helmet"
import { Link, graphql } from "gatsby"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const TagsPage = ({
  data: {
    allMarkdownRemark: { group },
    site: {
      siteMetadata: { title },
    },
  },
}) => (
  <Layout location={'/tags'} title={title} currentMenu={'tags'}>
    <Helmet title={title} />
    <div>
      <div className="mt-3 mb-3 pt-3 pb-3">
        <h5 style={{color: '#666666'}}>Tags</h5>
      </div>
      <div>
        {
          group
            .sort((tag1, tag2) => tag2.totalCount - tag1.totalCount)
            .map(tag => (
                <Link
                  key={tag.fieldValue}
                  to={`/tags/${kebabCase(toLower(tag.fieldValue))}/`}
                  className="badge badge-info p-2 m-1">
                  <FontAwesomeIcon icon={'tag'} size="xs" className={"mr-1"} />
                  <span className="mr-2">{tag.fieldValue}</span>
                  <span className="badge badge-light p-1">{tag.totalCount}</span>
                </Link>
            ))
        }
      </div>
    </div>
  </Layout>
)

TagsPage.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      group: PropTypes.arrayOf(
        PropTypes.shape({
          fieldValue: PropTypes.string.isRequired,
          totalCount: PropTypes.number.isRequired,
        }).isRequired
      ),
    }),
    site: PropTypes.shape({
      siteMetadata: PropTypes.shape({
        title: PropTypes.string.isRequired,
      }),
    }),
  }),
}

export default TagsPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(limit: 2000) {
      group(
        field: frontmatter___tags
      ) {
        fieldValue
        totalCount
      }
    }
  }
`